@import '../../global.scss';

.contact{
    background-color: rgb(26, 26, 30);
    display: flex;

    @include mobile{
        flex-direction: column;
    }

    .left{
        flex: 1;
        overflow: hidden;

        img{
            height: 100%;
        }
    }

    .right{
        flex: 1;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;

        h2{
            font-size: 30px;
            color: rgb(130, 87, 229);
        }

        form{
            width: 70%;
            height: 70%;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: space-around;

            input{
                width: 300px;
                height: 30px;
                font-size: 14px;

                @include mobile{
                    width: 200px;
                    height: 20px;
                }
            }
        }

        textarea{
            width: 300px;
            height: 200px;
            font-size: 14px;

            @include mobile{
                width: 200px;
                height: 100px;
            }
        }

        button{
            width: 150px;
            height: 30px;
            color: white;
            background-color: rgb(130, 87, 229);
            border: none;
            border-radius: 10px;
            font-weight: 500;
            cursor: pointer;

            &:focus{
                outline: none;
            }
        }

        span{
            color: rgb(130, 87, 229);
        }

    }
}